body {
  margin: 0;
  padding: 0;
  background-image: url(./background.jpg);
  background-size: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.char {
  width: 180px;
  height: 250px;
  background-color: rgba(217, 184, 255, 0.644);
  margin: 20px;
  display: inline-block;
  text-align: center;
  border: 4px rgba(49, 5, 46, 0.2) solid;
}

.charLiked {
  width: 180px;
  height: 340px;
  background-color: rgba(229, 206, 255, 0.568);
  margin: 20px;
  display: inline-block;
  text-align: center;
  border: 4px rgba(49, 5, 46, 0.2) solid;
}

.charLiked h1 {
  margin: 0;
  font-size: 25px;
  font-family: "starWarsFont";
}

.likeButton {
  position: absolute;
  width: 40px;
  height: 40px;
  margin-top: -80px;
  margin-left: 30px;
  border-radius: 50%;
  border: none;
  background-color: rgba(71, 223, 104, 0.575);
  outline: none;
}

.likeButton:hover {
  background-color: rgba(53, 255, 97, 0.712);
  box-shadow: 2px 2px 4px rgb(33, 223, 74), -2px -2px 4px rgb(33, 223, 74),
    -2px 2px 4px rgb(33, 223, 74), 2px -2px 4px rgb(33, 223, 74);
}

.likeButton:active {
  background-color: aqua;
}

.char:hover {
  box-shadow: 2px 2px 4px rgba(172, 49, 255, 0.801),
    -2px -2px 4px rgba(172, 49, 255, 0.801),
    -2px 2px 4px rgba(172, 49, 255, 0.801),
    2px -2px 4px rgba(172, 49, 255, 0.801);
}

.char h1 {
  margin: 0;
  font-size: 25px;
  font-family: "starWarsFont";
}

.charImg {
  margin-top: 10px;
  max-height: 200px;
  width: auto;
  object-fit: cover;
  border: 2px rgb(48, 4, 59) solid;
}

.header {
  width: 1000px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.footer {
  width: 1000px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  background-color: rgba(255, 0, 221, 0.1);
  border-radius: 40px;
}

.charBlock {
  width: 1000px;
  min-height: 200px;
  margin: 20px auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.lovedButton {
  width: 250px;
  height: 50px;
  font-family: "starWarsFont";
  font-size: 25px;
  margin: 10px;
}

.lovedCharButton {
  width: 250px;
  height: 50px;
  font-family: "starWarsFont";
  font-size: 25px;
  margin: 10px;
  background-color: rgba(189, 74, 255, 0.733);
  border-radius: 50px;
  color: white;
  border: 5px rgba(216, 88, 255, 0.623) solid;
  outline: none;
}

.lovedCharButton:hover {
  background-color: rgba(203, 91, 255, 0.795);
  border: 5px rgba(111, 9, 131, 0.623) solid;
}

.lovedCharButton:active {
  background-color: rgba(106, 2, 138, 0.945);
  border: 5px rgba(220, 130, 255, 0.623) solid;
}

.generalPageButton {
  width: 250px;
  height: 50px;
  font-family: "starWarsFont";
  font-size: 25px;
  margin: 10px;
  background-color: rgba(255, 92, 42, 0.596);
  border-radius: 50px;
  color: white;
  border: 5px rgba(255, 190, 104, 0.623) solid;
  outline: none;
}

.generalPageButton:hover {
  background-color: rgba(255, 81, 0, 0.795);
  border: 5px rgba(131, 48, 9, 0.623) solid;
}

.generalPageButton:active {
  background-color: rgba(138, 56, 2, 0.945);
  border: 5px rgba(255, 211, 130, 0.623) solid;
}

.lovedButtonChosen {
  width: 200px;
  height: 40px;
  font-family: "starWarsFont";
  font-size: 25px;
  margin: 10px;
  background-color: rgba(0, 238, 255, 0.8);
  box-shadow: 2px 2px 8px rgb(0, 238, 255), -2px -2px 8px rgb(0, 238, 255),
    2px -2px 8px rgb(0, 238, 255), -2px 2px 8px rgb(0, 238, 255);
  border: none;
  border-radius: 30px;
}

.lovedButtonReset {
  width: 200px;
  height: 40px;
  font-family: "starWarsFont";
  font-size: 25px;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.822);
  border-radius: 30px;
}

.lovedButton:disabled {
  width: 50px;
  height: 50px;
  font-family: "starWarsFont";
  font-size: 25px;
  color: white;
  margin: 10px;
  border-radius: 50%;
  background-color: rgba(162, 0, 255, 0.5);
  border: none;
}

.searchPanel {
  width: 100%;
  height: 50px;
  font-family: "starWarsFont";
  font-size: 25px;
  margin: 10px;
  outline: none;
}

.title {
  font-family: "starWarsFont";
  color: white;
  font-size: 40px;
  text-align: center;
}

.hide {
  display: none;
}

@font-face {
  font-family: "starWarsFont";
  src: url(./starWarsFont.otf);
}
